html,
body,
#root {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #121212;
    user-select: none;
}

* {
    box-sizing: border-box;
}

.timer_widget{
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fullscreen-widget{
    position: absolute;
    pointer-events: all;
    bottom: 10px;
    right: 10px;

    height: 40px;
    width: 40px;

    padding: 5px;
    border-radius: 5px;

    fill: white;

    cursor: pointer;

    &:hover{
        transform: scale(1.1);
        background-color: rgba(255, 255, 255, 0.3);
    }
}

.hand{
    cursor: pointer;
    pointer-events: all;
    padding: 10px;
    border-radius: 10px;
    width: 60px;
    display: flex;
    justify-content: center;

    svg{
        position: relative;
        fill: white;
        &:hover{
            transform: scale(1.1);
            transition: all ease-in-out 0.1s;
    
        }
    }

    .timer{
        position: absolute;
        font-family: 'Montserrat';
        font-size: 12px;
        bottom: 5px;
    }
}

.buzz{
    cursor: pointer;
    pointer-events: all;
    padding: 10px;
    border-radius: 10px;
    width: 60px;

    svg{
        position: relative;
        fill: white;
    }

    &:hover{
        transform: scale(1.1);
        transition: all ease-in-out 0.1s;
    }
}


.dashboard{
    padding: 25px;
    display: flex;
    gap: 10px;

    .widget{
        padding: 10px;
        width: min-content;
        border-radius: 10px;
        background-color: #1E1E1E;
        cursor: pointer;

        &:hover{
            background-color: #2C2C2C;
        }
    }
}

.interface {

    height: 100%;
    width: 100%;

    display: flex;

    .content{
        display: flex;
        flex-direction: column;
        flex:  1 1;
        overflow: auto;

        &::-webkit-scrollbar{
            display: none;
        }

        .container {
            display: flex;
            max-height: 100%;
            justify-content: center;
            align-items: center;

            .display {
                position: relative;
                height: fit-content;
                max-height: 100%;
                max-width: 100%;
                aspect-ratio: 16/9;

                display: flex;
                justify-content: center;
                flex: 1 1;

                border: none;
                background-color: black;

                .open_chat {
                    position: absolute;
                    pointer-events: all;
                    top: 15px;
                    right: 15px;
                    height: 30px;
                    width: 30px;
                    fill: white;
                    padding: 5px;
                    padding-top: 4px;
                    cursor: pointer;
                    border-radius: 5px;
                    transform: rotate(180deg);
        
                    &.left{
                        left: 15px;
                        transform: rotate(0deg);
                    }

                    &:hover {
                        background-color: rgba(255, 255, 255, 0.3);
                    }
        
                    @media screen and (max-width: 920px) {
                        visibility: hidden;
                    }
                }
        
                .vmix_tab {
                    width: auto; 
                    height: 100%; 
                    aspect-ratio: 16 / 9; 
                    border: none;
                    box-sizing: content-box;
                }
            
                .widget_container{
                    position: absolute;
                    height: fit-content;
                    width: max-content;
                    display: flex;
                    gap: 10px;
                    margin-top: 2vh;
                    background-color: rgba(255, 255, 255, 0.3);
                    border-radius: 25px;
                    padding: 5px 15px;
                }

                .on-top-display{
                    position: absolute;
                    pointer-events: none;
                    width: 100%;
                    height: 100%;
                    user-select: none;

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    @keyframes shake {
                        0% { transform: translateX(0); }
                        10% { transform: translateX(-15px); }  /* More extreme left shake */
                        20% { transform: translateX(15px); }   /* More extreme right shake */
                        30% { transform: translateX(-15px); }  /* More extreme left shake */
                        40% { transform: translateX(0); }
                        50% { transform: translateX(0); }
                        60% { transform: translateX(-15px); }  /* More extreme left shake */
                        70% { transform: translateX(15px); }   /* More extreme right shake */
                        80% { transform: translateX(-15px); }
                        90% { transform: translateX(0); }
                        100% { transform: translateX(-15px); }  /* More extreme left shake */
                    }

                    .buzz-display{
                        display: flex;
                        position: absolute;
                        pointer-events: none;
                        gap: 5px;
                        color: white;
                        width: 100%;
                        height: 100%;
                        align-items: center;
                        justify-content: center;

                        .buzz-action{
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            flex-direction: column;
                            width: 100%;
                            height: 100%;
                            background-color: rgba(0,0,0,0.6);
                            font-family: 'Montserrat';

                            .title{
                                font-weight: 800;
                                font-size: 60px;
                                color: #FFD700;
                                animation: shake 1s ease-in-out;
                                
                            }

                            .username{
                                font-weight: 500;
                                font-size: 25px;
                            }
                            .timestamp{
                                font-weight: 500;
                                font-size: 15px;
                            }
                        }
                    }

                    .wave-display{
                        position: absolute;
                        display: flex;
                        flex-direction: column;
                        pointer-events: none;
                        gap: 5px;
                        color: white;
                        width: 100%;
                        top: 12vh;
                        align-items: center;

                        .wave-action{
                            background-color: rgba(0,0,0,0.6);
                            padding: 2.5px 5px;
                            border-radius: 5px;
                            font-family: 'Montserrat';
                            color: pink;

                            &::after{
                                content: ' wants to talk!';
                                color: white;
                            }

                        }
                    }
                }
            }
        }
    }

    .rundown {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 339px;
        height: 100%;
        background-color: #141414;
        border-right: 1px solid #2C2C2C;
        color: white;
        font-family: 'Montserrat';

        @media screen and (max-width: 920px) {
            display: none;
        }

        .close_chat {
            position: absolute;
            top: 15px;
            right: 15px;
            height: 30px;
            width: 30px;
            fill: white;
            padding: 5px;
            padding-top: 4px;
            cursor: pointer;
            border-radius: 5px;
            transform: rotate(180deg);
            z-index: 10;

            &:hover {
                background-color: #2C2C2C;
            }
        }

        .content{
            padding: 10px;            
            display: flex;
            flex-direction: column;
            gap: 10px;


            .cell{
                background-color: #222222;
                padding: 10px;
                border-radius: 5px;
                display: flex;
                flex-direction: column;
                gap: 10px;
                position: relative;

                .shader{
                    height: 100%;
                    width: 100%;
                    top: 0;
                    left: 0;
                    border-radius: 5px;
                    position: absolute;
                    background-color: rgba(0,0,0,0.6);
                    pointer-events: none;
                }

                .title{
                    font-size: 20px;
                }

                .description{
                    font-size: 12px;
                
                    &.short{
                       
                        .description-content{
                            overflow: hidden;
                            height: 20px;
                        }

                        .see-more{
                            color: rgba(255, 255, 255, 0.3);
                            cursor: pointer;
                            &:hover{
                                color: rgba(255, 255, 255, 0.5);
                            }
                        }
                    }
                }
            }
        }

    }

    .chat {
        display: flex;
        flex-direction: column;
        width: 339px;
        height: 100%;
        background-color: #1E1E1E;
        border-left: 1px solid #2C2C2C;
        font-family: "Inter","Roobert","Helvetica Neue",Helvetica,Arial,sans-serif;

        @media screen and (max-width: 920px) {
            display: none;
        }

        .title {
            position: relative;
            height: 60px;
            flex: 0 0 100px;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            color: white;
            font-weight: 500;
            font-size: 20px;
            border-bottom: 1px solid #2C2C2C;

            flex-direction: column;

            .platform-switch{

                display: flex;
                gap: 10px;

                button{                        
                    border: none;
                    font-family: 'Montserrat';
                    border-radius: 5px;
                    padding: 2.5px 5px;
                    color: white;
                    background-color: #585858;
                    cursor: pointer;

                    &.twitch{
                        background-color: #6441a4;
                        &:hover{
                            background-color: #19171c;
                        }
                    }

                    &.youtube{
                        background-color: #ff0000;


                        &:hover{
                            background-color: #282828;
                        }
                    }
                }
            }

            .close_chat {
                position: absolute;
                top: 15px;
                left: 15px;
                height: 30px;
                width: 30px;
                fill: white;
                padding: 5px;
                padding-top: 4px;
                cursor: pointer;
                border-radius: 5px;

                &:hover {
                    background-color: #2C2C2C;
                }
            }
        }

        .chat_container {
            flex-grow: 1;
            overflow: auto;
            padding: 10px;


            .chat_feed {
                height: 100%;
                display: flex;
                flex-direction: column-reverse;
                overflow: auto;
                overflow-x: hidden;

                &::-webkit-scrollbar {
                    width: 6px;
                    /* Width of the scrollbar */
                }

                &::-webkit-scrollbar-track {
                    background: transparent;
                    /* Background of the scrollbar track */
                }

                &::-webkit-scrollbar-thumb {
                    background: #2C2C2C;
                    /* Scrollbar thumb color */
                    border-radius: 10px;
                    /* Rounded corners for the thumb */
                }

            }

            .message {

                padding: 5px 10px;
                border-radius: 5px;

                &.youtube{
                    display: flex;
                    gap: 10px;
                }

                &:hover {
                    background-color: #2C2C2C;
                }

                .photo{
                    border-radius: 50%;
                    height: 24px;
                    width: 24px;
                }

                .username {
                    display: inline;
                    color: white;
                    font-weight: 500;
                    font-size: 13px;

                }

                .content {
                    display: inline;
                    text-wrap: wrap;
                    font-size: 13px;
                    color: white;

                    &::before {
                        content: ': ';
                    }
                }

            }
        }
    }
}

.login {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }

    .border{
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        height: 40px;
        width: 350px;
        padding: 1px;
        border-radius: 5px;
    }

    .title {
        color: white;
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 15px;
    }

    input {
        background-color: #000;
        height: 100%;
        width: 100%;
        border: none;
        border-radius: 5px;
        outline: none;
        color: white;
        display: block;
        margin-right: 0;
        padding: 0 10px;
    }

    .invalid_email{
        font-size: 10px;
        font-weight: 600;
        width: 100%;
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        -webkit-background-clip: text;
        color: transparent;
    }

    .validate_login {
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        color: white;
        border: none;
        border-radius: 25px;
        height: 40px;
        width: 350px;
        margin-top: 15px;
        cursor: pointer;

        &:hover {
            opacity: 0.8;
        }
    }
}

.rank-rumble-widget{

    position: absolute;
    top: 10px;
    right: 10px;

    width: 25%;
    height: max-content;
    border-radius: 15px;
    padding: 25px;


    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgba(0,0,0,1);
    color: white;

    pointer-events: all;

    &:active {
        cursor: grabbing; /* Change cursor during dragging */
    }

    .play_container{
        width: 100%;
    }

    .access_room {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    
        .border_container {
            padding: 1px;
            border-radius: 10px;
            background: transparent;
            /* Gradient */
    
            .code_container {
                display: flex;
                flex-direction: column;
                box-sizing: border-box;
                gap: 15px;
                width: 300px;
                border-radius: 10px;
                padding: 15px;
    
                .border_input {
                    height: 40px;
                    background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                    /* Gradient */
                    border-radius: 5px;
                    box-sizing: border-box;
                    padding: 1px;
    
                    #private_code {
                        height: 100%;
                        width: 100%;
                        text-align: center;
                        padding: 0;
                        border: none;
                        outline: none;
                        color: white;
                        background-color: black;
                        border-radius: 5px;
    
                        &::-webkit-inner-spin-button,
                        &::-webkit-outer-spin-button {
                            -webkit-appearance: none;
                            margin: 0;
                        }
                    }
                }
    
    
    
                #submit_private_code {
                    height: 40px;
                    text-align: center;
                    outline: none;
                    background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                    border-radius: 5px;
                    border: 0;
                    color: white;
                    cursor: pointer;
    
                    &:hover {
                        opacity: 0.8;
                    }
                }
    
            }
        }
    
    
    }
    
    .waiting_room {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 15px;
        justify-content: center;
        align-items: center;
    
        .title {
            font-size: 50px;
            font-weight: 600;
            background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
            -webkit-background-clip: text;
            /* Clip the background to the text */
            color: transparent;
            text-align: center;
            /* Make the text itself transparent */
        }
    
        .subtitle{
            text-align: center;
        }
    }
    
    .quizz_room {
        height: 100%;
        width: 100%;
    
        .answer_container {
            height: 80%;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 10px;
    
            .question {
                line-height: 1;
                text-align: center;
                font-size: clamp(1px, 2vw, 100%);
                line-height: 1;
                margin-bottom: 20px;
            }
    
    
            .row {
                display: grid;
                width: 300px;
                height: 30px;
                grid-template-columns: 50px 1fr;
                column-gap: 10px;
                text-align: center;
    
                .border {
                    background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                    box-sizing: border-box;
                    border-radius: 5px;
                    padding: 1px;
    
                    .black_background {
                        background-color: #000;
                        border-radius: 5px;
                        height: 100%;
    
                        .rank {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color: #000;
                            height: 100%;
                            border-radius: 5px;
                        }
                    }
    
                    
                }
    
                .record {
                    background-color: #000;
                    border: none;
                    outline: none;
                    border-radius: 5px;
                    color: white;
                    text-align: center;
                    height: 100%;
                    width: 100%;
                }
            }
    
            .submit_answer {
                background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                width: fit-content;
                border: none;
                border-radius: 10px;
                color: white;
                padding: 10px 20px;
                margin-top: 20px;
                cursor: pointer;
                opacity: 0.8;
    
                &:hover{
                    background: linear-gradient(149deg, #6a0dad 0%, #1e90ff 96.86%);
                    opacity: 1;
                }
            }
    
    
        }
    }

    .leaderboard_room {
        height: 100%;
        width: 100%;
        background-color: #000;
    
    
        .table_container{
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    
        .table {
            display: grid;
            column-gap: 10px;
            width: 100%;
            row-gap: 10px;
            grid-template-columns: 30px 1fr 40px 75px;
    
            .key{
                background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 5px;
                height: 30px;
            }

            .border{
                background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
                padding: 1px;
                border-radius: 5px;
            }

            .rank,
            .points,
            .timestamp {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #000;
                border-radius: 5px;
                height: 100%;
                width: 100%;
            }

            .username {
                background-color: #000;
                border-radius: 5px;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                padding: 0 10px;
                width: 100%;
                height: 30px;
            }
        }
    }
}